main {
	margin: 0 auto;
	max-width: 90%;
  padding: 20px 30px;
}

:root {
  --primary: #85FFBD;
  /* --secondary: rgb(30, 96, 145); */
  --secondary: rgb(19, 69, 156);
  --tertiary: rgb(0, 52, 89);
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

h1, h2, h3, h4, p, .fab{
  color : var(--secondary);
}

h3 {
  text-align: center;
  margin-bottom: 20px;
}

/* h6 {
  color: var(--secondary);
  text-align: center;
  
} */

body {
  /* background-color: var(--primary);
  background-image: linear-gradient(179deg, #85FFBD 0%, #FFFB7D 100%); */
  /* background-image: linear-gradient(to top, #30cfd0 0%, #330867 100%); */
  /* background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%); */
  background-image: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);

}
/*header style begins*/
header{
  padding : 15px 50px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;  
}

/* header h1{
  color : var(--secondary);
  background: rgb(156, 232, 233); 
  font-size: 35px;
  padding: 5px;
  margin-bottom: 1rem;
} */

header nav {
  text-align: center;
}

header nav ul li {
  display:inline-flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  list-style: none;
  padding: 10px 0;
}

.section-name span {
  padding : 3px;
}

.section-name {
  color : var(--secondary);
  margin: 0 15px;
  font-weight:normal;
  font-size : 26px;
  text-decoration: none;
  padding: 3px;
  cursor: pointer;

  /* transition: all 0.2s ease-out; */
}

.section-name :hover {
  /* border: 1px solid var(--secondary) ; */
  background-color: var(--secondary);
  color: white;
  border-radius: 10px;
  /* padding: 5px; */
}

/* portfolio section begins */
.card {
  background-image: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);
  box-shadow: 5px 5px 5px lightgray; 
}

.image {
    position: relative;
    width: 500px;
    height: 300px;
    transition: 1s;
    background-image: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);
}

.image:hover {
  filter: brightness(75%);    
  cursor: pointer;
}

.card-git {
  text-align: center;
  display: block;
}


/*portfolio section ends */


/* About me section */
#img {
  width: 300px;
  height: 300px;
  padding: 10px 30px;
  margin-left: 12px;
  border-radius: 50%;
}

.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin: 0px 80px;
  font-size: 20px;
}

/* contact section styles */
#contact {
  color: var(--secondary);
  font-size: 20px;
}

/* #contact h3 {
  text-align: center;
} */

.btn-grad {background-image: linear-gradient(to right, #36D1DC 0%, #5B86E5  51%, #36D1DC  100%)}
.btn-grad {
   margin: 10px;
   padding: 15px 45px;
   text-align: center;
   text-transform: uppercase;
   transition: 0.5s;
   background-size: 200% auto;
   color: var(--tertiary);            
   box-shadow: 0 0 20px #eee;
   border-radius: 10px;
   display: block;
 }

 .btn-grad:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
 }

 #contact button {
   font-weight: bold;
 }
#error-message {
  padding: 0 15px;
}

/* resume section styles */
#icon-list{
    display: flex;
    justify-content: space-around;
    /* flex-direction: column; */
    /* align-items: center; */
    margin: 100px 0;
    text-align: center;
    color: var(--secondary);
}

/* #resume h3 {
  color : var(--secondary);
  text-align: center;
} */
.icons {
  display: flex;
  justify-content: space-between;
  padding-top: 10px;
}

#front-end, #back-end {
  display: flex;
    justify-content: space-evenly;
    flex-direction: column;
}

.icons svg {
  padding: 5px;
}

#resume-btn{
  font-weight: bold;
}

#resume button{
  margin : auto;
}

#resume h4 a {
  text-decoration: none;
}


/* footer section styles */
footer {
  bottom: 0;
  width: 100%;
  /* height: 2.5rem; */
  padding: 5px;
  display: flex;
  justify-content: center;
  margin-top: auto;
  margin: 10px;
  /* position: absolute;
  align-items: center; */
  cursor: pointer;
  
}

.footer-icon {
  padding : 0 15px;
}

/* ------------------------------------------------------ */
@media screen and (max-width: 980px) {

  .about {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 0px;
    font-size: 20px;
  }

  header {
    margin-right: 35px;
  }

  header h1{
      width: 100%;
      text-align: center;
      font-size: 70px;
  }

  header nav ul {
      margin-top: 20px;
      width: 100%;
      justify-content: center;
    }
  
    header nav ul li a {
      font-size: 40px;
    }

    #icon-list{
      display: contents;
      justify-content: space-around;
      /* flex-direction: column; */
      /* align-items: center; */
      margin: 100px 0;
      text-align: center;
      color: var(--secondary);
  }

  #front-end, #back-end {
    margin-bottom: 30px;
  }

  h3 {
    font-weight: bolder;
  }
  
  
}


